<style>
  .task-card .ivu-card-body {
    height: calc(100% - 32px);
    padding: 7px;
  }
  #task_card_list {
    line-height: 30px;
    height: 100%;
    padding: 7px;
    cursor: default;
  }
  .task-list-line-bottom {
    border-bottom: 1px solid #39383f;
  }
  .task-content-title-border {
    height: 60px;
    line-height: 60px;
    padding-left: 10px;
    position: relative;
  }
  .task-content-title-border:after {
    content: '';
    width: 100%;
    height: 1px;
    background-color: #39383f;
    position: absolute;
    top: 30px;
    left: 0;
    z-index: 0;
  }

  .task-content-title {
    background-color: #202025;
    position: absolute;
    padding-left: 4px;
    padding-right: 4px;
    z-index: 1;
  }

  .task-content-list {
    height: 360px;
  }
  .task-content-list .body-table {
    height:100%;
    display: flex;
    display: -webkit-flex;
    flex-direction:column;
    -webkit-flex-direction:column;
    -webkit-flex: 1 ;
    flex: 1 ;
    -moz-box-flex:  1 ;
  }

  #task_log_list, #task_content_remark {
    overflow: hidden;
    height: 95px;
    font-size: 12px;
    line-height: 14px;
    padding: 4px;
    border: 1px solid #39383f;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    border-radius: 5px;
  }
  .item-content-list {
    margin-left: 10px;
    width: calc(58.33333333% - 10px);
    border: 1px solid #39383f;
    padding: 5px;
  }
</style>
<template>
  <!--->->-->
  <div class="right-container" style="padding: 15px;">
    <Row class="container-navigation">
      <iCol span="2">
        <img src="../../../assets/content/navigate_icon.png" height="20" width="20"/>
      </iCol>
      <iCol span="22">
        <span class="page-title"></span>
      </iCol>
    </Row>
    <div class="back-to-list" v-if="canReturn" @click="returnPage"></div>
    <div style="height: 100%;">
      <Card style="width:100%; height: 100%;" class="grey task-card" :bordered="false">
        <p slot="title">
          <Icon type="ios-world-outline"></Icon>
          <span>{{task.taskName}}</span>
        </p>
        <a href="#" slot="extra">
          <Icon type="ios-loop-strong"></Icon>
        </a>
        <Row id="task_card_list">
          <Row class="task-list-line-bottom">
            <iCol span="8">
              <span>{{ getTaskStatus() }}</span>
            </iCol>
            <iCol span="8">
              <span>{{ getTaskType() }}</span>
            </iCol>
            <iCol span="8">
              <span class="text-ellipsis" :title="task.taskId">{{ task.taskId }}</span>
            </iCol>
          </Row>
          <Row class="task-list-line-bottom">
            <iCol span="8">
              <span :title="common.formatTime(task.planStartDt, 'yyyy-MM-dd HH:mm')">
                {{ common.formatLocal(task.planStartDt) }}
              </span>
            </iCol>
            <iCol span="8">
              <span :title="common.formatTime(task.planEndDt, 'yyyy-MM-dd HH:mm')">
                {{ common.formatLocal(task.planEndDt) }}
              </span>
            </iCol>
            <iCol span="8">
              <span :title="common.formatTime(task.checkOutDt, 'yyyy-MM-dd HH:mm')">
                {{ common.formatLocal(task.checkOutDt) }}
              </span>
            </iCol>
          </Row>
          <Row class="task-list-line-bottom">
            <iCol span="8" style="padding-left: 14px;">
              <span>{{ taskBaseInfo.primaryExecutorName }}</span>
            </iCol>
            <iCol span="8">
              <span>{{ taskBaseInfo.buildingName }}</span>
            </iCol>
          </Row>
          <div class="task-content-title-border">
            <div class="task-content-title">
              <span></span>
            </div>
          </div>
          <Row class="task-content-list">
            <iCol span="10" class="body-table">
              <Table :columns="taskItemColumns" border :data="taskItemList" class="auto-height-table grey"
                     @on-row-click="showItemContentDetail" style="width:100%">
              </Table>
            </iCol>
            <iCol span="14" class="body-table item-content-list" v-if="selectedTaskItem !== undefined">
              <div style="padding-left: 28px;">
                <span> {{selectedTaskItem.itemName}}</span>
              </div>
              <div>
                <span> {{selectedTaskItem.itemDesc}}</span>
              </div>
              <div style="padding-left: 14px;">
                <span> {{ getTaskItemStatus(selectedTaskItem.failed) }}</span>
              </div>
              <Table :columns="itemContentColumns" border :data="itemContentList" class="auto-height-table grey"
                    style="width:100%">
              </Table>
              <Row style="height: 50px; margin-top: 5px;">
                <iCol span="2"></iCol>
                <iCol span="22" style="height: 100%;">
                  <div id="task_content_remark" style="height: 100%;">
                    <p>{{ selectedTaskItem.remark }}</p>
                  </div>
                </iCol>
              </Row>
            </iCol>
          </Row>
          <div class="task-content-title-border">
            <div class="task-content-title">
              <span></span>
            </div>
          </div>
          <div id="task_log_list">
            <p v-for="log in operationLogList">
              <span>{{log.opTime}} {{log.opUser}}{{log.opComment}}</span>
            </p>
          </div>
        </Row>
      </Card>
    </div>
  </div>
</template>
<script>
  import niceScroll from 'jquery.nicescroll';
  import common from '../../../libs/common';
  import server from '../../../config/api';

  export default {
    data(){
      return {
        canReturn: this.getYunSessionLength('route_list') > 1,
        common: common,
//        common.formatLocal('2018-02-13 15:54:46.535')
        taskId: undefined,
        taskBaseInfo: {},
        task: {},
        taskItemList: [],
        taskItemColumns: [
          {
            type: 'index',
            align: 'center',
            width: '30'
          },
          {
            title: '',
            align: 'center',
            ellipsis: true,
            render: (h, params) => {
              return h('span', {
                domProps: {
                  title: params.row.itemName
                }
              }, params.row.itemName);
            }
          },
          {
            title: '',
            width: '70',
            align: 'center',
            render: (h, params) => {
              let result = '';
              if(params.row.itemContents === null || params.row.itemContents === undefined
                || params.row.itemContents.length === 0) {
                result = params.row.failed === null ? '' : '';
                return h('span', result);
              }
              let index = _.findIndex(params.row.itemContents, {failed: null});
              return h('span', index === -1 ? '' : '');
            }
          },
          {
            title: '',
            width: '70',
            align: 'center',
            ellipsis: true,
            render: (h, params) => {
              let result = this.getTaskItemStatus(params.row.failed);
              return h('span', {
                domProps: {
                  title: result
                }
              }, result);
            }
          },
        ],
        itemContentList: [],
        itemContentColumns: [
          {
            title: '',
            width: '120',
            align: 'center',
            ellipsis: true,
            render: (h, params) => {
              return h('span', {
                domProps: {
                  title: params.row.summary
                }
              }, params.row.summary);
            }
          },
          {
            title: '',
            align: 'center',
            ellipsis: true,
            render: (h, params) => {
              return h('span', {
                domProps: {
                  title: params.row.detail
                }
              }, params.row.detail);
            }
          },
          {
            title: '',
            width: '70',
            align: 'center',
            ellipsis: true,
            render: (h, params) => {
              let result = this.getTaskItemStatus(params.row.failed);
              return h('span', {
                domProps: {
                  title: result
                }
              }, result);
            }
          },
          {
            title: '',
            width: '120',
            align: 'center',
            ellipsis: true,
            render: (h, params) => {
              return h('span', {
                domProps: {
                  title: params.row.remark
                }
              }, params.row.remark);
            }
          },
        ],
        operationLogList: [],
        selectedTaskItem: undefined,
      };
    },
    methods: {
      returnPage: function() {
        // sessionStorage
        this.parentRoute = this.popYunSession('route_list');
        let page = this.topYunSession('route_list');
        this.$router.push(page.router);
      },
      initData: function() {
        let page = this.topYunSession('route_list');
        this.taskId = page.taskId;
        if(this.taskId === undefined) {
          this.$Message.error("");
          return;
        }
        this.getTaskDetail();
      },
      fetchData: function() {
        $(".auto-height-table .ivu-table-body").niceScroll({
          cursorcolor: "rgb(99, 114, 131)",
          cursorwidth: "7px",
          cursoropacitymax: 0.4,
          cursorborder: "", // css definition for cursor border
          cursorborderradius: "",
          hidecursordelay: 1000,
        });
        $("#task_log_list").niceScroll({
          cursorcolor: "rgb(99, 114, 131)",
//          railpadding: { top:0, right: -10, left: 0, bottom:0 },
          cursorwidth: "7px",
          cursoropacitymax: 0.4,
          cursorborder: "", // css definition for cursor border
          cursorborderradius: "",
          hidecursordelay: 1000,
        });
        $("#task_card_list").niceScroll({
          cursorcolor: "rgb(99, 114, 131)",
          railpadding: { top:0, right: -18, left: 0, bottom:0 },
          cursorwidth: "7px",
          cursoropacitymax: 0.4,
          cursorborder: "", // css definition for cursor border
          cursorborderradius: "",
          hidecursordelay: 1000,
        });
        // table
        $('#task_card_list').on('scroll',function(){
          $(".auto-height-table .ivu-table-body").getNiceScroll().resize();
          $("#task_log_list").getNiceScroll().resize();
          $("#task_content_remark").getNiceScroll().resize();
        });
      },
      getTaskDetail: function() {
        this.$http.get(server.api.routine.record.detail.replace('{taskId}', this.taskId)).then((response) => {
          this.taskItemList = (JSON.parse(response.body.data.task.taskContent)).taskItems;
          this.operationLogList = (JSON.parse(response.body.data.task.taskContent)).operationLogs;
          response.body.data.task.taskContent = undefined;
          this.task = response.body.data.task;
          response.body.data.task = undefined;
          this.taskBaseInfo = response.body.data;
        }, (error) => {
          this.$Message.error("");
        });
      },
      getTaskStatus: function() {
        let result = '';
        switch(this.task.status) {
          case 'OPEN': result = ''; break;
          case 'EXECUTING': result = ''; break;
          case 'DONE': result = ''; break;
        }
        return result;
      },
      getTaskType: function() {
        let result = '';
        switch(this.task.type) {
          case 'PATROL': result = ''; break;
          case 'UPKEEP': result = ''; break;
          case 'DETECT': result = ''; break;
        }
        return result;
      },
      showItemContentDetail: function(row, index) {
        this.selectedTaskItem = JSON.parse(JSON.stringify(row));
        this.itemContentList = this.selectedTaskItem.itemContents === null ? [] : this.selectedTaskItem.itemContents;
      },
      getTaskItemStatus: function(failed) {
        switch(failed) {
          case undefined:
          case null: return '';
          case 'PASS': return '';
          case 'FAIL': return '';
          case 'FAIL-I': return ' ';
          case 'FAIL-R': return ' ';
        }
      }
    },
    watch: {
      selectedTaskItem: function(val, oldVal) {
        if(val !== undefined) {
          this.$nextTick(() => {
            // 
            $(".auto-height-table .ivu-table-body").niceScroll({
              cursorcolor: "rgb(99, 114, 131)",
              cursorwidth: "7px",
              cursoropacitymax: 0.4,
              cursorborder: "", // css definition for cursor border
              cursorborderradius: "",
              hidecursordelay: 1000,
            });
            $("#task_content_remark").niceScroll({
              cursorcolor: "rgb(99, 114, 131)",
//          railpadding: { top:0, right: -10, left: 0, bottom:0 },
              cursorwidth: "7px",
              cursoropacitymax: 0.4,
              cursorborder: "", // css definition for cursor border
              cursorborderradius: "",
              hidecursordelay: 1000,
            });
          });
        }
      }
    },
    mounted(){
      this.initData();
      this.fetchData();
    }

  }
</script>



